.gf-product-info {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    border-bottom: 1px solid #ddd;
    @media (max-width: 992px) {
       flex-direction: column;
    }
    &__box {
        width: 100%;
        padding: 6px 0 0;
        @media (min-width: 992px) {
            width: 50%;
            padding: 30px 30px 30px 50px;
        }
    }
    &__image {
        // border: 1px solid $product-border;
        width: auto !important;
        // height: 100%;
        text-align: center;
        position: relative;
        // @media (min-width: 992px) {
        //     width: 49%;
        // }
    }
    &__detail {
        width: auto;
        padding: 70px;
        
        @media (max-width: 1200px) {
            padding:50px ;
        }
        @media (max-width: 991px) {
            padding:20px ;
        }
        @media (min-width: 992px) {
            width: 50%;
           
          
        }
    }
    &__title {
     
        margin-bottom: 0px;
        display: flex;
        justify-content: space-between;
        @media (max-width: 1200px) {
           flex-direction: column   ;
        }
        h2 {
            font-weight: 400;
            font-size: 26px;
            width: 100%;
            margin: 20px 0 10px 0;
            @media (min-width: 992px) {
                // width: calc(100% - 230px);
                margin: 0;
            }
        }
        h4.gf-ring-spacifacation {
            line-height: 21px;
            font-size: 14px;
            text-transform: capitalize;
            cursor: pointer;
            color: $black;
            a {
                color: $black;
                font-size: 16px;
                font-weight: 400;
                letter-spacing: 0;
                font-family: "Lato", sans-serif;
                span {
                    i {
                        font-size: 20px;
                    }
                }
            }
        }
    }
    &__descreption {
        margin-bottom: 15px;
        p {
            font-size: 14px;
            line-height: 20px;
            font-weight: 400;
            color: #828282;
        }
    }
    .diaomnd-info {
        .product-dropdown {
            margin-bottom: 8px;
            max-width: 360px;
            position: relative;
            background-color: #f2f2f2;
            width: 100%;
            padding: 10px;
            span {
                font-size: 16px;
                font-weight: 700;
                color: $black;
                text-transform: capitalize;
            }
            .metaldropdown {
                float: right;
                text-align: left;
                padding: 0 25px;
            }
            .stonesizedropdown {
                float: right;
                padding: 0 25px;
            }
            .ringdropdown {
                float: right;
                padding: 0 25px;
            }
            select {
                float: right;
                border: none;
                outline: none;
                background-color: transparent;
                text-align: right;
                padding: 0 25px;
            }
        }
    }
    p.image-note {
        color: $black;
        font-size: 18px;
        margin: 10px 0;
        clear: both;
    }
    .gf-product-controller {
        float: left;
        width: 100%;
        clear: both;
        margin-top: 10px;
        ul {
            li {
                padding: 15px 0;
                border-bottom: 1px solid #ddd;
               display: flex;
               justify-content: space-between;
                width: 100%;
                position: relative;
                // @media (min-width: 576px) {
                //     width: 50%;
                // }
                  
               span {
                    font-size: 16px;
                    font-weight: 500;
                    color: #828282;
                    transition: 0.3s;
                    display: flex;
                    align-items: center;
                    cursor: pointer;
                    // &::after{
                    //     position: absolute;
                    //     content: '\2715';                 
                    //     right: 0;
                    //     font-size: 18px;
                    //     transform: rotate(134deg);
                    //     top: 35%;
                    //     // transform: translate(0px, -50%);
                    // }
                    i{
                        margin-right: 15px;
                    }
                    svg{
                        height: 18px;
                        width: 18px;
                    }
                    &:hover {
                        color: $aqua;
                        span {
                            i {
                                background-color: $aqua;
                            }
                        }
                    }
                    span {
                        margin-right: 10px;
                        i {
                            padding: 6px;
                           
                            color: $white;
                            text-align: center;
                            font-size: 16px;
                            //  height: 28px;
                            width: 20px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }
                    }
                }
            }
        }
    }
    .gf-diamond-tryon {
        display: inline-block;
        width: 100%;
        .cart_gf_arrow{
            display: inline-block;
            font-size: 20px;
            margin-left: 10px;
        }
        .gf-diamond-btn {
            margin-top: 10px;
            width: 100%;
        }
        .btn-diamond {
            margin-right: 15px;
            width: 100%;
            @media (max-width: 991px) {
                width: 90%;
                margin-right: 0;
            }
        }
        .gf-cart-btns{
            height: 50px;
            font-size: 16px;
            border: 1px solid #fff;
            outline: 5px solid #fff;
            font-weight: 600;
        }
        span {
            display: block;
            font-size: 30px;
            font-weight: 600;
        }
    }
    .image-gallery-thumbnail {
        border: 1px solid $product-border;
        width: auto;
        margin: 3px;
    }
    .image-gallery-thumbnail-image {
        height: 50px;
        width: 50px;
        margin: 5px;
    }
    .top-icons {
        float: right;
        cursor: pointer;
        position: absolute;
        z-index: 1;
        padding: 20px;
        right: 0;
        span {
            margin-left: 10px;
            i {
                color: $icon-color;
                font-size: 22px;
            }
        }
    }
}

.diamond-information {
    ul {
        padding-left: 0;
        margin-left: 0;
        li {
            float: left;
            width: 100%;
            clear: both;
            padding: 11px 20px;
            box-sizing: border-box;
            margin-bottom: 0;
            list-style: none;
            font-family: "Lato", sans-serif;
            &:nth-child(even) {
                background-color: $border-color;
            }
            .diamonds-info {
                p {
                    text-align: right;
                }
            }
            div {
                float: left;
                width: 50%;
                p {
                    font-size: 14px;
                    color: $black;
                    @media (min-width: 576px) {
                        font-size: 16px;
                    }
                }
            }
        }
    }
    .spacification-info {
        width: 100%;
        background: #e8e8e8;
        display: table;
        vertical-align: middle;
        padding: 16px 20px;
        box-sizing: border-box;
        h2 {
            font-size: 20px;
            color: $black;
            font-family: "Lato", sans-serif;
            font-weight: 500;
            line-height: normal;
            width: calc(100% - 25px) !important;
            display: inline-block;
            letter-spacing: 3px;
        }
        h4 {
            font-size: 18px;
            display: inline-block;
            width: 25px !important;
            a {
                color: #828282;
            }
        }
    }
}

.gf-social-icons {
    padding: 20px 0;
    border-bottom: 1px solid #ddd;
    display: inline-block;
    width: 100%;
    span.gf_social_heading{
        font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    color: #828282;
    text-transform: capitalize;
    display: block;
    }
    .gf-social-share {
        display: flex;
        align-items: center;
        margin: 10px 0 0;
        float: left;
        li {
            
            z-index: 11;
            a {
                border-radius: 3px;
                color: $black;
                font-size: 11px;
                padding: 4px 5px;
                margin-right: 15px;
                span{
                    color: $black;
                }
                i {
                    margin-right: 10px;
                }
            }
        }
    }
}

.red {
    background-color: $red;
}

.sky-blue {
    background-color: $sky-blue;
    border-radius: 25px !important;
}

.blue {
    background-color: $blue;
}

.ring-diamond-image.gf-active {
    display: block;
}

.ring-diamond-image {
    .imageloader {
        text-align: center;
    }
    display: none;
    img {
        width: 60%;
    }
}

.ring-diamond-image-dia.gf-active {
    display: block;
}

.ring-diamond-image-dia {
    .imageloader {
        text-align: center;
    }
    display: none;
    img {
        width: 80%;
    }
}

.ring-diamond-video {
    display: none;
}

.ring-diamond-video.gf-active {
    display: block;
}
