.product-skus h2 {
    font-size: 20px;
    margin: 10px 0;
    text-align: center;
}

.image-thumb-image {
    margin-bottom: 20px;
    .image-gallary-box {
        display: flex;
        align-items: center;
        justify-content: center;
        .gf-active {
            border: 2px solid #000;
        }
        .image-gallary-list {
            // padding: 5px;
            border: 1px solid #ddd;
            margin: 5px;
        }
        .gf-active {
            border: 1px solid #000 !important;
        }
        img {
            width: auto;
            height: 50px;
            cursor: pointer;
            @media (min-width: 1400px) {
                width: 100%;
                height: 50px;
            }
        }
    }
}

.gf-diamond-intro-field {
    max-width: 100%;
    float: left;
    width: 100%;
    margin: 0 0 15px;
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        width: 45%;
        float: left;
        box-sizing: border-box;
        &:nth-child(1) {
            border-right: 1px solid #9f9f9f;
            li {
                padding-right: 30px;
            }
        }
        &:nth-child(2) {
            padding-left: 30px;
        }
        li {
            display: table;
            vertical-align: middle;
            width: 100%;
            padding: 9px 10px;
            box-sizing: border-box;
            padding-left: 0;
            strong {
                display: table-cell;
                font-size: 18px;
                color: #828282;
                width: 50%;
            }
            p {
                text-align: left;
                display: table-cell;
                font-size: 18px;
            }
        }
    }
}

.diamond-image {
    text-align: center;
}

.diamond-report-link {
    text-align: center;
    p {
        font-size: 16px;
        color: $black;
        font-weight: 700;
        font-family: "Lato", sans-serif;
        a {
            color: $black;
            text-decoration: underline;
        }
    }
}

.diamond-grade {
    display: flex;
    align-items: center;
    background: #f7f7f7;
    padding: 17px 20px;
    margin-top: 10px;
    p {
        padding-left: 15px;
        font-size: 16px;
        color: $black;
        font-family: "Lato", sans-serif;
    }
    .grade-image {
        img {
            width: 100px;
            height: 100px;
        }
    }
}

.gf-internam-use {
    p {
        font-size: 16px;
        color: $black;
        font-weight: 700;
        font-family: "Lato", sans-serif;
        a {
            color: $black;
            text-decoration: underline;
        }
    }
}

.internaluseform {
    margin-top: 20px;
    #auth_password {
        width: 100%;
        font-size: 16px;
        outline: none;
        border-radius: 0;
        margin-bottom: 10px;
        border: 1px solid #ddd;
    }
    .gf-btn {
        display: block;
