.gf-details_product_container{
    width: 100%;
   
    @media (max-width: 991px) {
        width: 95%;
    }
    .gf-diamond-back-button{
        margin: 0 0 20px;
        @media (max-width: 767px) {
            margin: 0 0 20px 0 ;
        }
    }
}

.gf_product_thumb_left_row{
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    @media (max-width: 767px) {
        flex-direction: column;
    }

    .image-thumb-image .image-gallary-box{
        flex-direction: column;
        @media (max-width: 767px) {
            flex-direction: row;
        }
    }
    .image-thumb-image{
        width: 10%;
        margin-bottom: 0;
        @media (max-width: 767px) {
            width: 100%;
            order: 2;
        }
        .image-gallary-list {
            height: 80px;
            width: 80px;
            display: flex;
            align-items: center;
            border: 1px solid #ddd;
            img{
                height: 100%;
                width: 97%;
            }
        }
    }
    .ring-diamond-image-dia{
        width: 85%;
        @media (max-width: 767px) {
            width: 100%;
           
        }
    }

    .ring-diamond-video{
        width: 68%;
        margin: 0 auto;
        @media (max-width: 767px) {
            width: 100%;
           
        }
    }
   
}
.gf-product-info__title_Desc{
    width: 62%;
    @media (max-width: 1200px) {
        width: 100%;
     }
}
.gf-ring-price{
    span{
        font-size: 22px;
        margin-bottom: 10px;
        display: block;    
    }
}
.gf-popup-email{
    margin-top: 0 !important;
    ul{
        li{
            border-bottom: 0 !important;
            &::after{
                content: none !important;
            }
        }
    }
}